import Vue from 'vue'
import Router from 'vue-router'
import VueCookies from 'vue-cookies'

import Dashboard from '../screens/Dashboard'
import VesselView from '../screens/VesselView'
import EngineFaults from '../screens/EngineFaults'
import EnginePerformance from '../screens/EnginePerformance'
import UserSettings from '../screens/UserSettings'
import Register from '../screens/Register'
import Login from '../screens/Login'
import Alerts from '../screens/Alerts'
import DailyReport from '../screens/DailyReport'
import FaultsHistory from '../screens/FaultsHistory'

import {globalStore} from "../main.js"

Vue.use(Router);
Vue.use(VueCookies);

var router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      beforeEnter: (to, from, next) => {
        // console.log('/');
        // console.log(from);
        //console.log(to);
          const { uri } = to.query;
          if (uri != null && uri != '/') {
              next(false);
              router.push(uri);
          } else {
              next();
          }
      },
      redirect: {
       name:"Login"
        //name: "Dashboard"
      }
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/vessel-view/:vessel',      
      name: 'Vessel View',
      component: VesselView
    },
    {
      path: '/engine-faults',
      name: 'Engine Faults',
      component: EngineFaults
    },
    {
      path: '/faults-history/:id',
      name: 'Faults History',
      component: FaultsHistory
    },
    {
      path: '/engine-performance',
      name: 'Engine Performance',
      component: EnginePerformance
    },
    {
      path: '/user-settings',
      name: 'Settings',
      component: UserSettings
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
    },
    {
      path: '/alerts',
      name: 'Alerts',
      component: Alerts,
    },
    {
      path: '/report',
      name: 'Report',
      component: DailyReport,
    }
  ]
});

router.beforeEach((to, from, next) => { 
  // console.log('before each');
  // console.log(from);
  // console.log(to);
  if ((to.path=='/login')||(globalStore.userLogged)) {
    next()    
  }    
  else {
    next('/login')
  }    
})


